<template>
<div>
<div class="wrap">
<div class="Avatar_box  border-bottom-solid">
    <ul class="Avatar">
    <li>
		<div class="left">头像</div>
        <router-link to="/Avatar_edit">
				<div class="right">
	                <div class="img"><img :src="personDate.headimgurl"></div>
			         <i class="iconfont icon-youhua"></i>
			  </div>
	      </router-link>
		</li>

    </ul>
</div>

	<ul ref="userList"  class="userList">

		 <li>
				<div class="left">昵称</div>
				<div class="right">
					 <input type="text" readonly  name="nickname"  v-model="personDate.nickname">
	                <i class="iconfont icon-youhua"></i>
			      </div>
		</li>

		<li>
			
					<div class="left">真实姓名</div>
					<div class="right">
					 <input type="text" readonly  name="user_name"  v-model="personDate.user_name">
	                 <i class="iconfont icon-youhua"></i>
			        </div>

		</li>


		<li>
		
					<div class="left">性别</div>
					<div class="right">
					<input type="text" readonly  name="sex"  v-model="personDate.sex">
	                 <i class="iconfont icon-youhua"></i>
			        </div>
	
		</li>

		<li>
		
					<div class="left">出生年月日</div>
					<div class="right">
					<input type="text" readonly  name="birthday"  v-model="personDate.birthday">
	                 <i class="iconfont icon-youhua"></i>
			     </div>
			
		</li>


		<li>
			
					<div class="left">手机号码</div>
					<div class="right">
					<input type="text" readonly  name="tel_number"  v-model="personDate.tel_number">
	                <i class="iconfont icon-youhua"></i>
			   </div>
			
		</li>
		<li>
			
					<div class="left">地址</div>
					<div class="right">
					<input type="text" readonly  name="detail_address"  v-model="personDate.detail_address">
	                 <i class="iconfont icon-youhua"></i>
			        </div>
			</a>
		</li>
		<li>
					<div class="left">社区</div>
					<div class="right">
					<input type="text" readonly  name="community"  v-model="personDate.community">
	                 <i class="iconfont icon-youhua"></i>
			        </div>
			</a>
		</li>
	</ul>

</div>




</div>

</template>

<script type="text/ecmascript-6">
import { Toast } from 'mint-ui'
 import footer from "@/components/base/footer"
 import {addClass} from '../../../static/js/dom.js'
 export default {
   data(){
    return{
     personDate:{
        "nickname": "aghron",
        "balance": "0.00",
        "integral": 780,
        "headimgurl": require('../../../static/img/touxiang.jpg'),
        "user_name": "张三",
        "sex": "男",
        "birthday": "1999-09-29",
        "tel_number": "13049646464",
        "detail_address": "广东省广州市天河区林和东路",
        "community": "逸景翠园"
    }
     
    }
  },
  components: {
      MFooter:footer
   },
  created(){
},

mounted () {
       this.init(),
       this.edit()
      },


methods:{

  init(){
        let li=this.$refs.userList.getElementsByTagName("li")
        for(let i of li){
         addClass(i, 'border-bottom-solid')
        }
  },
  edit(){
  	 var that=this
  	 let li=this.$refs.userList.getElementsByTagName("li")
        for(let i of li){
	         i.addEventListener("click", function(){
	         let userData=i.getElementsByTagName("input")[0].value
	         let item=i.getElementsByTagName("input")[0].name
	         that.$router.push({name:'edit',params: {item:item,userData: userData}})
      })
        }

  }


       
 }

}
 
</script>

<style lang="stylus" type="text/stylus" scoped>
.Avatar_box
.Avatar li
 	height:0.8rem
 	.left
 		height:0.5rem
 		line-height:0.8rem
 	.right
       	.img
       		display:inline-block
       		border-radius:50%
       		width:0.68rem
	        height:0.68rem
	        box-sizing:border-box
       		overflow:hidden
	        img
	        	height:100%
	        	width:auto
	         	vertical-align:middle
	    .iconfont
	    	height:0.5rem
	    	line-height:0.8rem
.userList,.Avatar
	   	width:90%
	   	margin:0 auto
	   	clear:both
	    li 
	     	margin-top:15px
	     	height:0.6rem	
	     	line-height:0.57rem
	     	width:100%
	     .left 
		       	float:left
		       	font-size:0.29rem
		       	color:#333
	     .right
		       	float:right
		       	display:flex
		        padding-bottom:5px
		        box-sizing:border-box
		        input[type=text]
		        	border:none
		        	text-align:right
		        	font-size:0.27rem
		        	color:#666
		        	
	     .iconfont
	    	 font-size:0.2rem
	    	 margin-left:8px
	    	 

       

 	
</style>



